@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@200;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Satisfy&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
}

h1, h2, h3, h4, h5, h6, p, button, input, select, option, a {
  font-family: 'Source Sans Pro', sans-serif;
}

h1, h2, h3 {
  font-weight: bold;
}

p, button, input, select, option {
  font-weight: normal;
}

a {
  text-decoration: none;
  color: #000000;
}

span {
  background-color: none;
  border: none;
}

button {
  font-size: 12px;
}

a:hover, button span:hover {
  color: #65b2b2;
}

section {
  min-height: 100%;
}

.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  font-size: 24px;  /* Preferred icon size */
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  font-feature-settings: 'liga';
}

.material-icons.md-18 { font-size: 18px; }
.material-icons.md-24 { font-size: 24px; }
.material-icons.md-36 { font-size: 36px; }
.material-icons.md-48 { font-size: 48px; }

.calendar-wrapper {
  width: 1440px;
  height: 700px;
  display: flex;
  flex-direction: row;
  margin: auto;
}

.calendar {
  width: 900px;
  height: 600px;
  margin: auto;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
}

.calendar-header {
  width: 100%;
  height: 50px;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.calendar-body {
  margin: auto;
}

.title {
  width: 25%;
  height: 100%;
  display: flex;
  align-items: center;
}

.title h2 {
  margin: auto;
}

.tools {
  width: 25%;
  height: 100%;
  display: flex;
  align-items: center;
}

.tools button {
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  background-color: #ffffff;
  border: none;
}

.tools button span:hover {
  color: #99cccc;
}

.table-header {
  width: 100%;
  display: flex;
}

.weekday {
  width: 20%;
  height: 50px;
  display: flex;
}

.weekday p {
  margin: auto;
}

.table-content {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  box-sizing: border-box;
}

.calendarDay {
  width: 125px;
  height: 75px;
  border: 1px solid #a6a6a6;
  position: relative;
}

.calendarDay:hover {
  background-color: rgba(0,0,0,.10);
}

.calendarDay p {
  position: absolute;
  top: 10px;
  right: 10px;
  color: #a6a6a6
}

.current p {
  color: #000000;
}

.selected p {
  color: #cc0000;
  font-weight: bold;
}



.day-card {
  width: 384px;
  height: 576px;
  margin: auto;
  background-color: rgba(0,0,0,.05);
  display: flex;
  flex-direction: column;
  padding: 5px;
  border-radius: 10px;
}

.day-card .header {
  width: 75%;
  height: 20%;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content:space-evenly;
}

.day-card .header h2 {
  text-align: center;
}

.day-card hr {
  background-color: #65b2b2;
  width: 100%;
  height: 3px;
  border: none;
}

.events-wrapper {
  width: 90%;
  flex-grow: 1;
  min-height: 20%;
  max-height: 80%;
  margin: auto;
  display: flex;
  flex-direction: column;
}

.event {
  width: 100%;
  height: 25px;
  margin-bottom: 2rem;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.event-content {
  width: 90%;
  height: 100%;
  margin: auto;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.event-tools {
  width: 10%;
  height: 100%;
  margin: auto;
  align-items: center;
  display: flex;
}

.event-tools button {
  background-color: none;
  margin: auto;
  display: flex;
  border: none;
}

.event-tools span {
  margin: auto;
  color: #cc0000;
}

.event-tools span:hover {
  background-color: #cc0000;
  color: #ffffff;
}

.no-events {
  width: 100%;
  height: 50px;
}

.no-events h4 {
  line-height: 2em;
}

.event p {
  margin-right: 25px;
  margin-left: auto;
}

