nav {
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  background-color: rgba(0,0,0,.05);
  justify-content: space-between;
}

.nav-header {
  width: 200px;
  height: 100%;
  display: flex;
  background-color: #65b2b2;
  justify-content: space-around;
  align-items: center;
}

.nav-header h1 {
  margin: auto;
  font-family: 'Satisfy', cursive;
  font-size: 1.5rem;
}

.nav-menu {
  width: 300px;
  height: 100%;
  display: flex;
  margin-right: auto;
  align-items: center;
}

.nav-menu a {
  margin: auto;
}

.nav-menu #selected {
  text-decoration: underline;
  text-underline-offset: .75rem;
}

.nav-profile {
  width: 100px;
  height: 100%;
  display: flex;
  align-items: center;
}

.nav-tools span, .nav-tools button, .nav-profile span, .nav-profile button {
  margin: auto;
  border: none;
  background-color: rgba(0,0,0, .0);
}





